
























	import orderItem from '../../components/orderItem.vue'
	import goodsOrderItem from '../../components/goodsOrderItem.vue'
	import ut from '../../utils/index.js';
	export default {
		props:['type','comment','show'],
		components: {
            orderItem,
			goodsOrderItem
        },
		data() {
			return {
				order_list: [],
				cancel_reason: [],
				goods_list:[]
			}
		},
		watch:{
			comment(){
				console.log(1111111)
				this.init();
			},
			show(){
				if(this.show&&wx.getStorageSync('token')){
					this.init();
				}else{
					this.order_list=[]
					this.goods_list=[]
				}
			}
		},
		onLoad(option) {
			/* this.comment=option.comment
			let barTitle = "查看订单";
			if(this.type == 1) {
				barTitle = "查看订单";
			}else if(this.type == 2) {
				barTitle = "确认施工方案和价格";
			}else if(this.type == 3) {
				barTitle = "质量验收及付尾款";
			}
			wx.setNavigationBarTitle({
			  title: barTitle
			}) */
			this.init();
		},
		methods: {
			init() {
				this.order_list=[]
				this.goods_list=[]
				if(this.comment==2){
					this.getOrderList1();
				}else{
					this.getOrderList();
				}
			},
			getOrderList() {
				ut.request({
					data: {
						type: this.type
					},
					method: 'get',
					url: "service/order/list"
				}).then(data=>{
					console.log(data)
					this.order_list = data;
				})
			},
			getOrderList1() {
				ut.request({
					data: {
						type: this.type
					},
					method: 'get',
					url: "goods/order/goodsOrderList"
				}).then(data=>{
					console.log(data)
					data.forEach(item=>{
						item.orderGoods.forEach(item=>{
							if(item.picture)item.picture=item.picture.split(',')[0];
						})
					})
					this.goods_list = data;
				})
			}
		}
	}
